.canvasWrapper {
  position: relative;
  border: solid black 2px;
  width: 100%;
  height: 100%;
}

.canvas {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
}

.bottomCanvas {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
}

.selectionRect {
  fill: none;
  stroke-width: 1;
  stroke-dasharray: 5 5 5 5;
}
